import api from '@/common/api.js'
import { parseObj } from '@/common/util.js'
export default {
	data() {
		return {
			listData: [],
			hasLoad: false,
			pageNum: 1,
			pageSize: 10,
			isLoading: false,
			hasMore: false
		}
	},
	mounted() {
		this.load();
	},
	methods: {
		load() {
			let params = {
				professionId: getApp().globalData.professionId,
				courseId: getApp().globalData.courseId,
				pageNum: this.pageNum,
				pageSize: this.pageSize
			};
			if (getApp().globalData.course.curriculums) {
				let ids = [];
				ids.push(getApp().globalData.course.id);
				params.curriculumIds = ids.join(',');
			}
			api.getMyCollect(params).then((result)=>{
				if (result.code == 0) {
					let list = result.data || [];
					this.hasMore = list.length == this.pageSize;
					let data = this.listData;
					if (this.isLoading) {
						for (let i in list) {
							data.push(list[i]);
						}
						this.isLoading = false;
					} else {
						data = list;
					}
					this.listData = data,
					this.hasLoad = true;
				} else {
					this.hasLoad = true;
				}
			})
		},
		onItemClick(data) {
			let params = {
				id: data.id
			};
			uni.navigateTo({
				url: '/pages/index/singletimu?' + parseObj(params)
			})
		}
	}
}
